import React from "react";
// import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import ShareIcon from "@material-ui/icons/Share";
import { /*Avatar, */IconButton, CardMedia,Dialog,DialogActions, DialogContent, /*DialogContentText,*/ DialogTitle } from "@material-ui/core";

const CoffeCard = props => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const {  title, liga,dia,  imageUrl,m3u81 } = props;
  return (
    <div>
    <Card>
      <CardHeader
        // avatar={<Avatar src={avatarUrl} />}
        action={
          <IconButton aria-label="settings">
            <ShareIcon />
          </IconButton>
        }
        title={liga}
        subheader={dia}
      />
      <CardMedia style={{ height: "150px" }} image={imageUrl} />
      <CardContent>
        <Typography variant="body2" component="p">
          {title}
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small" onClick={handleClickOpen}>Abrir</Button>
        <Button color="secondary" /*size="small"*/>Estadisticas</Button>
      </CardActions>
    </Card>

<Dialog
open={open}
onClose={handleClose}
aria-labelledby="alert-dialog-title"
aria-describedby="alert-dialog-description"
>
<DialogTitle id="alert-dialog-title">{"Opciones Disponibles"}</DialogTitle>
<DialogContent>
<Button variant="outlined" color="primary" href={m3u81}>
  OPCION 1
</Button> &nbsp; 
<Button variant="outlined" color="primary" href={m3u81}>
  OPCION 2
</Button> &nbsp; 
<Button variant="outlined" color="primary" href={m3u81}>
  OPCION 3
</Button> &nbsp; 
</DialogContent>
<DialogActions>
  <Button onClick={handleClose} color="primary">
    Disagree
  </Button>
  <Button onClick={handleClose} color="primary" autoFocus>
    Agree
  </Button>
</DialogActions>
</Dialog></div>
  );
};

export default CoffeCard;
